	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Laid-up Layout","date":"05 Aug 2000","slug":"/2000-08-05-laid-up-layout/","tags":"Y2000"},"html":"<p>Mama didn't like the Aqua theme, so I've switched to Basic Blog. I set the font to 12 points... I hope that works for everyone, including Grandma!</p>\n<hr />\n<font color=\"maroon\">Okay, now that our home page doesn't resemble the front of our kitchen stove anymore I can contribute something!</p>\n<p>I've kept busy keeping Christopher from pulling <a href=\"http://shanson.editthispage.com/pictures/viewer$317\">cables</a>. Yes, any electrical cords that we've tried to hide he'll go after.<br />\n<a href=\"http://w3.one.net/~voyager/candl.html\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/08/cl.jpg\" height=\"123\" width=\"118\" align=\"left\" alt=\"c&l: \" border=\"0\" /></a> I've re-discovered my favorite TV series <a href=\"http://w3.one.net/~voyager/candl.html\">Cagney and Lacey</a>. The re-runs are on every afternoon and are a highlight in my daily routine.</font></p>"},"next":{"fields":{"title":"PhotoPoint","date":"04 Aug 2000","slug":"/2000-08-04-photopoint/"}},"previous":{"fields":{"title":"CyClassics","date":"06 Aug 2000","slug":"/2000-08-06-cyclassics/"}}},{"node":{"fields":{"title":"PhotoPoint","date":"04 Aug 2000","slug":"/2000-08-04-photopoint/","tags":"Y2000"},"html":"<p>Mama hasn't written anything for PapaScott yet, but she has been busy taking pictures for <a href=\"http://albums.photopoint.com/j/AlbumList?u=185392\">PhotoPoint</a>. We made it easier for her to send pictures there by putting the account in her name. She can now send the pictures in via E-mail direct from the camera, and PhotoPoint automatically resizes the files. It wastes lots of bandwidth, but is much easier to work with.</p>\n<p>Christopher is now bending his knees when he lays on his stomach. He just has to get his butt a little higher, and he'll be crawling!</p>"},"next":{"fields":{"title":"Thursday","date":"03 Aug 2000","slug":"/2000-08-03-thursday/"}},"previous":{"fields":{"title":"Laid-up Layout","date":"05 Aug 2000","slug":"/2000-08-05-laid-up-layout/"}}},{"node":{"fields":{"title":"Thursday","date":"03 Aug 2000","slug":"/2000-08-03-thursday/","tags":"Y2000"},"html":"<p>A better caption for yesterday's picture might have been: \"Look what happened to the cat dish that used to be here...\" (In the interest of child safety and of nutrition, for both the child and the cats, the cat dishes have been temporarily relocated.)</p>"},"next":{"fields":{"title":"Glad it's over","date":"02 Aug 2000","slug":"/2000-08-02-glad-its-over/"}},"previous":{"fields":{"title":"PhotoPoint","date":"04 Aug 2000","slug":"/2000-08-04-photopoint/"}}}],"pathPrefix":"","first":false,"last":false,"index":1048,"pageCount":1101}}

